body {
  position: relative;
  font-family: "Roboto", Helvetica, Arial, sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 1.56;
  min-width: 320px;
  color: #000;
  background-color: #fff;
}

.title {
  font-size: 38px;
  color: #0074e4;
}

.title_lg {
  font-size: 45px;
  line-height: 1.11;
}

.title_xs {
  font-size: 20px;
  line-height: 2.5;
  letter-spacing: -0.8px;
}

.title_white {
  color: #fff;
}

.inner {
  max-width: 1026px;
  padding: 0 15px;
  margin: 0 auto;
}

.header {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  color: #fff;
  padding-top: 21px;
  font-size: 14px;
  z-index: 5;
}

.header__inner {
  max-width: 1230px;
  margin: 0 auto;
  padding: 0 15px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header__left {
  display: flex;
}

.header__menu {
  display: flex;
  align-items: center;
  margin-left: 57px;
}

.logo {
  font-size: 39px;
}

.logo .icon {
  display: block;
}

.main-menu {
  display: flex;
}

.main-menu__item:not(:first-child) {
  margin-left: 40px;
}

.language {
  position: relative;
  text-transform: uppercase;
  padding-right: 17px;
  cursor: pointer;
}

.language__current {
  display: flex;
  align-items: center;
}

.language__current .icon {
  margin-right: 6px;
}

.language__arrow {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  right: 0;
  font-size: 4px;
}

.language__arrow .icon {
  display: block;
}

.language__list {
  position: absolute;
  left: 0;
  right: 0;
  text-align: center;
  top: 100%;
  opacity: 0;
  visibility: hidden;
  transition: opacity .3s, visibility .3s;
}

.language:hover .language__list {
  opacity: 1;
  visibility: visible;
}

.language__item {
  padding: 0 17px 0 19px;
}

.language__link {
  position: relative;
  display: inline-block;
  border-bottom: 1px solid transparent;
  transition: border-color .3s;
}

.language__link:before {
  content: "";
  position: absolute;
  top: -3px;
  bottom: -3px;
  left: -10px;
  right: -10px;
}

.language__link:hover {
  border-color: currentColor;
}

.main {
  position: relative;
  background-image: linear-gradient(to bottom, #20232d, #151720);
  overflow: hidden;
  color: #fff;
}

.main:after {
  content: "";
  position: absolute;
  width: 200%;
  height: 400px;
  left: 0;
  top: 100%;
  background-color: #fff;
  transform-origin: 0 0;
  transform: rotate(-5deg);
}

.main__inner {
  position: relative;
  z-index: 2;
}

.main__content {
  max-width: 418px;
  padding-right: 10px;
}

.main__wrapper {
  padding: 100px 0 50px;
  height: 100vh;
  min-height: 700px;
  display: flex;
  align-items: center;
}

.main__left, .main__right {
  width: 50%;
}

.main__right {
  align-self: flex-end;
}

.main__img {
  width: 960px;
  margin-left: -92px;
}

.main__img img {
  max-width: 100%;
  display: block;
}

.main__title {
  margin: 7px 0 25px;
}
